<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>配料报警</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form inline ref="queryForm" :model="queryForm" label-width="80px" class="form_border">
        <el-form-item label="设备名称">
          <el-input v-model="queryForm.deviceName" placeholder="请输入设备名称"></el-input>
        </el-form-item>
        <el-form-item label="设备Mac">
          <el-input v-model="queryForm.deviceMac" placeholder="请输入设备名称"></el-input>
        </el-form-item>
        <el-form-item label="报警类型">
          <el-select v-model="queryForm.alarmType" placeholder="请选择报警类型" clearable>
            <el-option v-for="item in alarmTypes" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="报警状态">
          <el-select v-model="queryForm.handleState" placeholder="请选择报警状态" clearable>
            <el-option v-for="item in handleStates" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="开始时间">
          <el-date-picker
            @change="
              value => {
                changeDate(value, 'startDate')
              }
            "
            v-model="queryForm.startDate"
            type="date"
            placeholder="选择开始有效时间"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间" prop="endDate">
          <el-date-picker
            @change="
              value => {
                changeDate(value, 'endDate')
              }
            "
            v-model="queryForm.endDate"
            type="date"
            placeholder="选择结束有效时间"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
      </el-form>
      <div class="MT_table_cont">
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="deviceName" label="设备名称"> </el-table-column>
          <el-table-column prop="alarmType" label="报警类型">
            <template slot-scope="scope">
              {{ alarmType(scope.row.alarmType) }}
            </template>
          </el-table-column>
          <el-table-column prop="handleState" label="报警状态">
            <template slot-scope="scope">
              {{ handleState(scope.row.handleState) }}
            </template>
          </el-table-column>
          <el-table-column label="创建时间" width="180">
            <template slot-scope="scope">
              {{ dateFormat(scope.row.alarmTime) }}
            </template>
          </el-table-column>
          <el-table-column prop="handleUsername" label="处理人" width="80"> </el-table-column>
          <el-table-column label="处理时间" width="180">
            <template slot-scope="scope">
              {{ dateFormat(scope.row.updateDatetime) }}
            </template>
          </el-table-column>
          <el-table-column label="操作" width="180">
            <template slot-scope="scope">
              <el-link type="primary" :underline="false" @click="alarmDetail(scope.row.id)">查看日志</el-link>
              <el-link type="primary" :underline="false" @click="clearAlarm(scope.row.id)">清除报警</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
        </div>
      </div>
    </el-card>
    <!--查看报警日志 -->
    <el-dialog title="报警信息" :visible.sync="detailDialog" width="35%">
      <el-divider></el-divider>
      <div v-if="!detailData.length">暂无信息</div>
      <el-timeline v-else :reverse="true">
        <el-timeline-item v-for="(item, index) in detailData" :key="index" :color="item.color">
          <el-row>
            <el-col :span="8">
              <span style="margin-left: 20px">{{ item.handleUsername || '未知' }}</span>
            </el-col>
            <el-col :span="8">
              <span style="margin-left: 40px">{{ handleState(item.handleState) }}</span>
            </el-col>
            <el-col :span="8">
              <span style="margin-left: 40px">{{ dateFormat(item.createDatetime) }}</span>
            </el-col>
          </el-row>
          <!-- <span style="margin-left: 40px">{{ dateFormat('', '', item.createDatetime, '') }}</span> -->
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import { alarmTypes, handleStates } from '@/utils/constant'
export default {
  props: ['url'],
  data() {
    return {
      queryForm: {
        deviceMac: '',
        deviceName: '',
        startDate: '',
        endDate: '',
        size: 10,
        page: 1,
        total: '',
        alarmType: '',
        handleState: ''
      },
      tableData: [],
      detailData: [],
      selectId: '',
      detailDialog: false,
      alarmTypes: alarmTypes,
      handleStates: handleStates
    }
  },
  computed: {
    alarmType() {
      return alarmType => {
        return this.$alarmType(alarmType)
      }
    },
    handleState() {
      return handleState => {
        return this.$handleState(handleState)
      }
    },
    dateFormat() {
      const that = this
      return data => {
        return this.$moment(data).format('YYYY-MM-DD HH:MM:SS')
      }
    }
  },
  methods: {
    //报警日志弹框
    alarmDetail(id) {
      this.selectId = id
      this.searchDetail(id)
      this.detailDialog = true
    },
    //查询报警日志
    searchDetail(id) {
      this.httpPost('/device/alarm/detail/' + id, '').then(res => {
        if (res.length) res[res.length - 1].color = '#0bbd87'
        this.detailData = res
      })
    },
    clearAlarm(id) {
      this.httpPost('/device/alarm/clear/' + id, '').then(res => {
        this.$message.success('清除成功')
        this.loadData()
      })
    },
    // 搜索
    search() {
      // console.log(this.queryForm)
      if (!this.queryForm.startDate && this.queryForm.endDate) return this.$message({ type: 'warning', message: '请选择开始日期!' }), false
      if (this.queryForm.startDate && !this.queryForm.endDate) return this.$message({ type: 'warning', message: '请选择结束日期!' }), false
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.count = ''
      this.loadData()
    },
    changeDate(e, type) {
      // debugger
      console.log(this.queryForm.startDate, this.queryForm.endDate)
      if (this.queryForm.endDate && type === 'startDate' && e && e.getTime() > new Date(this.queryForm.endDate).getTime()) {
        this.$message.warning('开始时间不得大于结束时间')
        this.queryForm.startDate = ''
      } else if (this.queryForm.startDate && e && e.getTime() < new Date(this.queryForm.startDate).getTime()) {
        this.$message.warning('结束时间不得小于开始时间')
        this.queryForm.endDate = ''
      }
    },
    loadData() {
      const that = this
      let params = Object.assign({}, that.queryForm)
      params.startDate = params.startDate ? that.$moment(params.startDate).format('YYYY-MM-DD') : ''
      params.endDate = params.endDate ? that.$moment(params.endDate).format('YYYY-MM-DD') : ''
      that.httpGet(that.url, params).then(res => {
        that.tableData = res.rows
        that.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 200px !important;
  }
}
</style>
